html,
body {
	height: 100%;
	margin: 0;
	padding: 0;
	background: rgb(255, 162, 77);
	background: linear-gradient(180deg, rgba(255, 162, 77, 1) 0%, rgba(155, 57, 232, 1) 100%);
	background-attachment: fixed;
	text-align: center;
}

.style-font {
	font-family: "Lobster", cursive;
	text-align: center;
	color: white;
}

p {
	font-family: "Nunito", sans-serif;
}

.p-main-page {
	text-align: center;
	color: white;
}

.container {
	display: flex;
	flex-direction: column;
	flex-wrap: wrap;
	align-items: center;
	justify-content: center;
	height: 100vh;
	max-width: 50vw;
}

.logo {
	width: 8rem;
}

.drag,
.drop {
	display: inline-block;
	width: 100px;
	height: 100px;
	margin: 5px auto;
	line-height: 5em;
	text-align: center;
	background-color: #110a65;
	color: white;
}

.drop {
	position: absolute;
	right: 0;
	top: 10;
}
